import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { Users, UserPlus, Search, Shield, Briefcase, FileText, Activity, Save, X, Lock, Clock } from 'lucide-react';
import { usePharmaStore } from '../store/useStore';
import { useLocationStore } from '../store/useLocationStore';
import { ROLES, Role, Permission } from '../../domain/security/roles';
import { EmployeeProfile } from '../../domain/types';
import { APP_MODULES, ROLE_PRESETS } from '../../domain/config/roles_presets';
import AttendanceManager from './hr/AttendanceManager';
import { WebAuthnService } from '../../infrastructure/biometrics/WebAuthnService';
import { toast } from 'sonner';

const HRPage = () => {
    const { user, employees } = usePharmaStore();
    const [activeTab, setActiveTab] = useState<'STAFF' | 'ATTENDANCE'>('STAFF');
    const [searchTerm, setSearchTerm] = useState('');
    const [selectedEmployee, setSelectedEmployee] = useState<EmployeeProfile | null>(null);
    const [isEditing, setIsEditing] = useState(false);

    // --- Access Control ---
    if (!user || user.role !== 'MANAGER') {
        return (
            <div className="flex flex-col items-center justify-center h-full text-slate-500">
                <Shield size={64} className="text-red-500 mb-4" />
                <h2 className="text-2xl font-bold text-slate-800 mb-2">Acceso Denegado</h2>
                <p>No tienes permisos para acceder al m√≥dulo de Recursos Humanos.</p>
                <p className="text-sm mt-2">Rol requerido: MANAGER</p>
            </div>
        );
    }

    const filteredEmployees = employees.filter(emp =>
        emp.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        emp.rut.includes(searchTerm)
    );

    const handleSave = (e: React.FormEvent) => {
        e.preventDefault();
        // Here we would call an updateEmployee action from the store
        console.log('Saving employee:', selectedEmployee);
        setIsEditing(false);
        setSelectedEmployee(null);
    };

    return (
        <div className="h-full flex flex-col bg-slate-50 overflow-hidden">
            {/* Main Header with Tabs */}
            <div className="bg-white border-b border-slate-200 px-6 pt-6 pb-0">
                <div className="flex justify-between items-center mb-6">
                    <div>
                        <h1 className="text-2xl font-bold text-slate-800 flex items-center gap-2">
                            <Users className="text-blue-600" />
                            Recursos Humanos
                        </h1>
                        <p className="text-slate-500">Gesti√≥n de personal, contratos y asistencia</p>
                    </div>
                    {activeTab === 'STAFF' && (
                        <button
                            onClick={() => {
                                setSelectedEmployee({
                                    id: `EMP-${Date.now()}`,
                                    rut: '',
                                    name: '',
                                    role: 'CASHIER',
                                    access_pin: '',
                                    status: 'ACTIVE',
                                    current_status: 'OUT',
                                    job_title: 'CAJERO_VENDEDOR', // Default
                                    allowed_modules: []
                                } as EmployeeProfile);
                                setIsEditing(true);
                            }}
                            className="bg-blue-600 text-white px-4 py-2 rounded-xl font-bold hover:bg-blue-700 transition-colors flex items-center gap-2 shadow-lg shadow-blue-200"
                        >
                            <UserPlus size={20} />
                            Nuevo Empleado
                        </button>
                    )}
                </div>

                <div className="flex gap-8">
                    <button
                        onClick={() => setActiveTab('STAFF')}
                        className={`pb-4 px-2 font-bold text-sm transition-colors relative flex items-center gap-2 ${activeTab === 'STAFF' ? 'text-blue-600' : 'text-slate-500 hover:text-slate-700'}`}
                    >
                        <Users size={18} />
                        Personal
                        {activeTab === 'STAFF' && <div className="absolute bottom-0 left-0 w-full h-1 bg-blue-600 rounded-t-full" />}
                    </button>
                    <button
                        onClick={() => setActiveTab('ATTENDANCE')}
                        className={`pb-4 px-2 font-bold text-sm transition-colors relative flex items-center gap-2 ${activeTab === 'ATTENDANCE' ? 'text-blue-600' : 'text-slate-500 hover:text-slate-700'}`}
                    >
                        <Clock size={18} />
                        Gesti√≥n de Asistencia
                        {activeTab === 'ATTENDANCE' && <div className="absolute bottom-0 left-0 w-full h-1 bg-blue-600 rounded-t-full" />}
                    </button>
                </div>
            </div>

            {/* Content Area */}
            <div className="flex-1 overflow-hidden">
                {activeTab === 'ATTENDANCE' ? (
                    <AttendanceManager />
                ) : (
                    <div className="h-full flex gap-6 p-6 overflow-hidden">
                        {/* Employee List */}
                        <div className="w-1/3 bg-white rounded-2xl shadow-sm border border-slate-200 flex flex-col overflow-hidden">
                            <div className="p-4 border-b border-slate-100">
                                <div className="relative">
                                    <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400" size={18} />
                                    <input
                                        type="text"
                                        placeholder="Buscar por nombre o RUT..."
                                        value={searchTerm}
                                        onChange={(e) => setSearchTerm(e.target.value)}
                                        className="w-full pl-10 pr-4 py-2 bg-slate-50 border border-slate-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500"
                                    />
                                </div>
                            </div>
                            <div className="flex-1 overflow-y-auto p-2 space-y-2">
                                {filteredEmployees.map(emp => (
                                    <div
                                        key={emp.id}
                                        onClick={() => { setSelectedEmployee(emp); setIsEditing(true); }}
                                        className={`p-3 rounded-xl cursor-pointer transition-all ${selectedEmployee?.id === emp.id ? 'bg-blue-50 border-blue-200 ring-1 ring-blue-300' : 'hover:bg-slate-50 border border-transparent'}`}
                                    >
                                        <div className="flex justify-between items-start">
                                            <div>
                                                <h3 className="font-bold text-slate-800">{emp.name}</h3>
                                                <p className="text-xs text-slate-500 font-bold">{emp.job_title?.replace(/_/g, ' ')}</p>
                                                <p className="text-[10px] text-slate-400">Rol: {ROLES[emp.role]}</p>
                                            </div>
                                            <div className={`w-2 h-2 rounded-full mt-1 ${emp.status === 'ACTIVE' ? 'bg-emerald-500' : 'bg-slate-300'}`} />
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>

                        {/* Employee Details / Edit Form */}
                        <div className="flex-1 bg-white rounded-2xl shadow-sm border border-slate-200 overflow-y-auto p-8">
                            {isEditing && selectedEmployee ? (
                                <form onSubmit={handleSave} className="max-w-2xl mx-auto space-y-8">
                                    <div className="flex items-center justify-between border-b border-slate-100 pb-6">
                                        <h2 className="text-xl font-bold text-slate-800 flex items-center gap-2">
                                            {selectedEmployee.id.startsWith('EMP-') && !selectedEmployee.rut ? <UserPlus className="text-blue-500" /> : <Briefcase className="text-blue-500" />}
                                            {selectedEmployee.id.startsWith('EMP-') && !selectedEmployee.rut ? 'Nuevo Empleado' : 'Editar Empleado'}
                                        </h2>
                                        <div className="flex gap-2">
                                            <button type="button" onClick={() => { setIsEditing(false); setSelectedEmployee(null); }} className="p-2 hover:bg-slate-100 rounded-lg text-slate-500">
                                                <X size={20} />
                                            </button>
                                            <button type="submit" className="bg-blue-600 text-white px-6 py-2 rounded-xl font-bold hover:bg-blue-700 shadow-lg shadow-blue-200 flex items-center gap-2">
                                                <Save size={18} />
                                                Guardar Cambios
                                            </button>
                                        </div>
                                    </div>

                                    {/* Personal Info */}
                                    <div className="space-y-4">
                                        <h3 className="text-sm font-bold text-slate-400 uppercase tracking-wider flex items-center gap-2">
                                            <FileText size={16} />
                                            Informaci√≥n Personal
                                        </h3>
                                        <div className="grid grid-cols-2 gap-4">
                                            <div>
                                                <label className="block text-sm font-bold text-slate-700 mb-1">Nombre Completo</label>
                                                <input
                                                    type="text"
                                                    value={selectedEmployee.name}
                                                    onChange={e => setSelectedEmployee({ ...selectedEmployee, name: e.target.value })}
                                                    className="w-full p-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 outline-none"
                                                />
                                            </div>
                                            <div>
                                                <label className="block text-sm font-bold text-slate-700 mb-1">RUT</label>
                                                <input
                                                    type="text"
                                                    value={selectedEmployee.rut}
                                                    onChange={e => setSelectedEmployee({ ...selectedEmployee, rut: e.target.value })}
                                                    className="w-full p-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 outline-none"
                                                />
                                            </div>
                                            <div>
                                                <label className="block text-sm font-bold text-slate-700 mb-1">Tel√©fono Contacto</label>
                                                <input type="text" autoComplete="tel" className="w-full p-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 outline-none" value={selectedEmployee.contact_phone || ''} onChange={e => setSelectedEmployee({ ...selectedEmployee, contact_phone: e.target.value })} />
                                            </div>
                                            <div>
                                                <label className="block text-sm font-bold text-slate-700 mb-1">Estado</label>
                                                <select className="w-full p-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 outline-none" value={selectedEmployee.status} onChange={e => setSelectedEmployee({ ...selectedEmployee, status: e.target.value as any })}>
                                                    <option value="ACTIVE">Activo</option>
                                                    <option value="ON_LEAVE">Licencia/Vacaciones</option>
                                                    <option value="TERMINATED">Desvinculado</option>
                                                </select>
                                            </div>
                                        </div>
                                    </div>

                                    {/* Contract Info */}
                                    <div className="space-y-4">
                                        <h3 className="text-sm font-bold text-slate-400 uppercase tracking-wider flex items-center gap-2">
                                            <Briefcase size={16} />
                                            Contrato y Rol
                                        </h3>
                                        <div className="grid grid-cols-2 gap-4">
                                            <div>
                                                <label className="block text-sm font-bold text-slate-700 mb-1">Cargo (Contrato)</label>
                                                <select
                                                    value={selectedEmployee.job_title}
                                                    onChange={e => setSelectedEmployee({ ...selectedEmployee, job_title: e.target.value as any })}
                                                    className="w-full p-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 outline-none"
                                                >
                                                    <option value="QUIMICO_FARMACEUTICO">Qu√≠mico Farmac√©utico</option>
                                                    <option value="AUXILIAR_FARMACIA">Auxiliar de Farmacia</option>
                                                    <option value="CAJERO_VENDEDOR">Cajero Vendedor</option>
                                                    <option value="BODEGUERO">Bodeguero</option>
                                                    <option value="ADMINISTRATIVO">Administrativo</option>
                                                    <option value="GERENTE_GENERAL">Gerente General</option>
                                                </select>
                                            </div>
                                            <div>
                                                <label className="block text-sm font-bold text-slate-700 mb-1">Rol del Sistema</label>
                                                <select
                                                    value={selectedEmployee.role}
                                                    onChange={(e) => {
                                                        const newRole = e.target.value as Role;
                                                        // Auto-populate modules based on role preset
                                                        const presetModules = ROLE_PRESETS[newRole] || [];
                                                        setSelectedEmployee({
                                                            ...selectedEmployee,
                                                            role: newRole,
                                                            allowed_modules: [...presetModules] // Copy to allow manual override
                                                        });
                                                    }}
                                                    className="w-full p-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 outline-none"
                                                >
                                                    {Object.entries(ROLES).map(([key, label]) => (
                                                        <option key={key} value={key}>{label}</option>
                                                    ))}
                                                </select>
                                            </div>
                                            <div>
                                                <label className="block text-sm font-bold text-slate-700 mb-1">Sueldo Base (CLP)</label>
                                                <input type="number" className="w-full p-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 outline-none" value={selectedEmployee.base_salary || 0} onChange={e => setSelectedEmployee({ ...selectedEmployee, base_salary: Number(e.target.value) })} />
                                            </div>
                                            <div>
                                                <label className="block text-sm font-bold text-slate-700 mb-1">Horas Semanales</label>
                                                <input type="number" className="w-full p-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 outline-none" value={selectedEmployee.weekly_hours || 45} onChange={e => setSelectedEmployee({ ...selectedEmployee, weekly_hours: Number(e.target.value) })} />
                                            </div>
                                            <div>
                                                <label className="block text-sm font-bold text-slate-700 mb-1">AFP</label>
                                                <select className="w-full p-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 outline-none" value={selectedEmployee.pension_fund || ''} onChange={e => setSelectedEmployee({ ...selectedEmployee, pension_fund: e.target.value })}>
                                                    <option value="">Seleccionar...</option>
                                                    <option value="HABITAT">Habitat</option>
                                                    <option value="MODELO">Modelo</option>
                                                    <option value="PLANVITAL">Planvital</option>
                                                    <option value="PROVIDA">Provida</option>
                                                    <option value="CAPITAL">Capital</option>
                                                    <option value="CUPRUM">Cuprum</option>
                                                    <option value="UNO">Uno</option>
                                                </select>
                                            </div>
                                            <div>
                                                <label className="block text-sm font-bold text-slate-700 mb-1">Salud</label>
                                                <select className="w-full p-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 outline-none" value={selectedEmployee.health_system || ''} onChange={e => setSelectedEmployee({ ...selectedEmployee, health_system: e.target.value })}>
                                                    <option value="">Seleccionar...</option>
                                                    <option value="FONASA">Fonasa</option>
                                                    <option value="ISAPRE_CONSALUD">Consalud</option>
                                                    <option value="ISAPRE_CRUZBLANCA">Cruz Blanca</option>
                                                    <option value="ISAPRE_COLMENA">Colmena</option>
                                                    <option value="ISAPRE_BANMEDICA">Banm√©dica</option>
                                                    <option value="ISAPRE_VIDATRES">Vida Tres</option>
                                                    <option value="ISAPRE_NUEVAMASVIDA">Nueva Masvida</option>
                                                </select>
                                            </div>
                                        </div>
                                    </div>

                                    {/* Security */}
                                    <div className="space-y-4">
                                        <h3 className="text-sm font-bold text-slate-400 uppercase tracking-wider flex items-center gap-2">
                                            <Lock size={16} />
                                            Seguridad
                                        </h3>
                                        <div>
                                            <label className="block text-sm font-bold text-slate-700 mb-1">PIN de Acceso (4 d√≠gitos)</label>
                                            <input
                                                type="password"
                                                maxLength={4}
                                                value={selectedEmployee.access_pin}
                                                onChange={e => setSelectedEmployee({ ...selectedEmployee, access_pin: e.target.value })}
                                                className="w-32 p-3 bg-slate-50 border border-slate-200 rounded-xl focus:ring-2 focus:ring-blue-500 outline-none font-mono text-center tracking-widest"
                                            />
                                        </div>

                                        {/* Module Permissions */}
                                        <div>
                                            <label className="block text-sm font-bold text-slate-700 mb-3">Permisos Adicionales (M√≥dulos)</label>
                                            <div className="grid grid-cols-2 gap-4">
                                                {['OPERATIVO', 'LOGISTICA', 'ADMIN', 'GERENCIA'].map(category => (
                                                    <div key={category} className="bg-slate-50 p-4 rounded-xl border border-slate-100">
                                                        <h4 className="text-xs font-bold text-slate-400 uppercase mb-3">{category}</h4>
                                                        <div className="space-y-2">
                                                            {APP_MODULES.filter(m => m.category === category).map(module => (
                                                                <label key={module.id} className="flex items-center gap-2 cursor-pointer group">
                                                                    <div className={`w-5 h-5 rounded border flex items-center justify-center transition-colors ${selectedEmployee.allowed_modules?.includes(module.id) ? 'bg-blue-500 border-blue-500' : 'bg-white border-slate-300 group-hover:border-blue-400'}`}>
                                                                        {selectedEmployee.allowed_modules?.includes(module.id) && <CheckCircle size={12} className="text-white" />}
                                                                    </div>
                                                                    <input
                                                                        type="checkbox"
                                                                        className="hidden"
                                                                        checked={selectedEmployee.allowed_modules?.includes(module.id)}
                                                                        onChange={(e) => {
                                                                            const currentModules = selectedEmployee.allowed_modules || [];
                                                                            if (e.target.checked) {
                                                                                setSelectedEmployee({
                                                                                    ...selectedEmployee,
                                                                                    allowed_modules: [...currentModules, module.id]
                                                                                });
                                                                            } else {
                                                                                setSelectedEmployee({
                                                                                    ...selectedEmployee,
                                                                                    allowed_modules: currentModules.filter(m => m !== module.id)
                                                                                });
                                                                            }
                                                                        }}
                                                                    />
                                                                    <span className={`text-sm ${selectedEmployee.allowed_modules?.includes(module.id) ? 'text-slate-700 font-medium' : 'text-slate-500'}`}>
                                                                        {module.label}
                                                                    </span>
                                                                </label>
                                                            ))}
                                                        </div>
                                                    </div>
                                                ))}
                                            </div>
                                        </div>

                                        {/* Biometrics */}
                                        <div className="space-y-4">
                                            <h3 className="text-sm font-bold text-slate-400 uppercase tracking-wider flex items-center gap-2">
                                                <span className="text-xl">üñêÔ∏è</span>
                                                Biometr√≠a
                                            </h3>
                                            <div className="bg-slate-50 p-4 rounded-xl border border-slate-100 flex items-center justify-between">
                                                <div>
                                                    <p className="font-bold text-slate-700">Huella Digital / Rostro</p>
                                                    <p className="text-xs text-slate-500">
                                                        {selectedEmployee.biometric_credentials?.length
                                                            ? '‚úÖ Biometr√≠a Configurada'
                                                            : '‚ö†Ô∏è No configurada'}
                                                    </p>
                                                </div>
                                                <button
                                                    type="button"
                                                    onClick={async () => {
                                                        try {
                                                            toast.info('Iniciando registro biom√©trico...');
                                                            const credential = await WebAuthnService.registerCredential(selectedEmployee.id, selectedEmployee.name);
                                                            if (credential) {
                                                                const currentCreds = selectedEmployee.biometric_credentials || [];
                                                                setSelectedEmployee({
                                                                    ...selectedEmployee,
                                                                    biometric_credentials: [...currentCreds, credential.id]
                                                                });
                                                                toast.success('Biometr√≠a registrada exitosamente');
                                                            }
                                                        } catch (error) {
                                                            toast.error('Error al registrar biometr√≠a');
                                                        }
                                                    }}
                                                    className="px-4 py-2 bg-white border border-slate-200 rounded-lg text-sm font-bold text-slate-700 hover:bg-slate-100 transition shadow-sm"
                                                >
                                                    {selectedEmployee.biometric_credentials?.length ? 'Agregar Otra' : 'Vincular Huella'}
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            ) : (
                                <div className="h-full flex flex-col items-center justify-center text-slate-400">
                                    <Users size={64} className="mb-4 text-slate-200" />
                                    <p className="text-lg font-medium">Selecciona un empleado para ver detalles</p>
                                </div>
                            )}
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
};

function CheckCircle({ size, className }: { size: number, className?: string }) {
    return (
        <svg
            xmlns="http://www.w3.org/2000/svg"
            width={size}
            height={size}
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            strokeWidth="3"
            strokeLinecap="round"
            strokeLinejoin="round"
            className={className}
        >
            <polyline points="20 6 9 17 4 12" />
        </svg>
    );
}

export default HRPage;
